import React from "react";
import { makeStyles } from "@material-ui/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Checkbox from "@material-ui/core/Checkbox";
import Avatar from "@material-ui/core/Avatar";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: 500
    // backgroundColor: theme.palette.background.paper
  },
  check: {
    width: 30,
    height: 30
  }
}));

const q = [
  "Designated handicap parking with a priority location",
  "Step free access (level or ramped) and/or lift access to main entrance",
  "Elevator to above ground accessible accommodation",
  "Ground level/lobby level accessible washroom"
];

const q2 = [
  "Wider entry and bathroom doorways – external 80 cm, internal 75 cm",
  "Maneuvering space on each side of the bed – 90 cm",
  "Wheeled shower chair and/or wall mounted shower seat",
  "Raised toilet"
];

function CheckboxListSecondary() {
  const classes = useStyles();
  const [checked, setChecked] = React.useState([1]);

  const handleToggle = value => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  return (
    <List dense className={classes.root}>
      <span>Public Area</span>
      {[0, 1, 2, 3].map(value => (
        <ListItem key={value} button>
          <ListItemAvatar>
            <Avatar
              alt={`Avatar n°${value + 1}`}
              src={`https://github.com/iglizer/galgalim/blob/master/i${value + 1}.png?raw=true`}
            />
          </ListItemAvatar>
          <ListItemText primary={q[value]} />
          <ListItemSecondaryAction>
            {value < 3 && (
              <Avatar
                className={classes.check}
                src={`https://github.com/iglizer/galgalim/blob/master/icon_128.png?raw=true`}
              />
            )}
            {value >= 3 && (
              <Avatar
                className={classes.check}
                src={`https://github.com/iglizer/galgalim/blob/master/Cancel-512.png?raw=true`}
              />
            )}
          </ListItemSecondaryAction>
        </ListItem>
      ))}
      <span>Room</span>
      {[0, 1, 2, 3].map(value => (
        <ListItem key={value} button>
          <ListItemAvatar>
            <Avatar
              alt={`Avatar n°${value + 1}`}
              src={`https://github.com/iglizer/galgalim/blob/master/i${value + 5}.png?raw=true`}
            />
          </ListItemAvatar>
          <ListItemText primary={q2[value]} />
          <ListItemSecondaryAction>
            {value < 4 && (
              <Avatar
                className={classes.check}
                src={`https://github.com/iglizer/galgalim/blob/master/icon_128.png?raw=true`}
              />
            )}
            {value >= 4 && (
              <Avatar
                className={classes.check}
                src={`https://github.com/iglizer/galgalim/blob/master/Cancel-512.png?raw=true`}
              />
            )}
          </ListItemSecondaryAction>
        </ListItem>
      ))}
    </List>
  );
}

export default CheckboxListSecondary;
